/* eslint-disable no-irregular-whitespace */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">

       <!--  -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="" prop="user_email"></el-table-column>
         <el-table-column label="ID" prop="book_id"></el-table-column>
         <el-table-column label="" prop="book_name"></el-table-column>
         <el-table-column label="" prop="lend_time"></el-table-column>
         <el-table-column label="" prop="telephone"></el-table-column>
       </el-table>
        <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total,sizes,prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        // 
        pagenum: 1,
        // 
        pagesize: 5,
        query: ''
      },
      booklist: [],
      total: 0
    }
  },
  created () {
    this.getBookList()
  },
  methods: {

    // 
    async getBookList () {
      // 
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectlendbook', {
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
    },
    // pagesize
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookList()
    },
    // 
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookList()
    }
  }
}
</script>

<style lang="less" scoped>
.editbtn{
  float: right;
  line-height: 15px;
}
</style>
