/* eslint-disable no-irregular-whitespace */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
       <el-row gutter="20">
         <el-col :span="8">
           <el-input placeholder="" v-model="queryInfo.query" clearable
           @clear="getBookList">
             <el-button slot="append" icon="el-icon-search" @click="selectBookQuery"></el-button>
          </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="addDialogVisible = true"></el-button>
          </el-col>
       </el-row>

       <!--  -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="ID" prop="bookid"></el-table-column>
         <el-table-column label="" prop="name"></el-table-column>
         <el-table-column label="" prop="press"></el-table-column>
         <el-table-column label="" prop="author"></el-table-column>
         <el-table-column label="" prop="price"></el-table-column>
         <el-table-column label="(/)" prop="is_shelf"></el-table-column>
         <el-table-column label="" width="180px">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeBookbyId(scope.row.bookid)"></el-button>
              </el-tooltip>
             <!--  -->
             <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
               <el-button type="warning" icon="el-icon-more" size="mini" @click="showinfoDialog(scope.row.bookid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>
        <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%" @close="addDialogClosed">
      <!-- -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" label-width="100px">
        <el-form-item style="margin-top:-30px">
        </el-form-item >
        <el-form-item label="" prop="name">
          <el-input type="textarea" autosize v-model="addForm.name" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="outline">
          <el-input type="textarea" autosize v-model="addForm.outline"></el-input>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input type="textarea" autosize v-model="addForm.detail" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="press">
          <el-input type="textarea" autosize v-model="addForm.press" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="publish_date">
           <el-input type="textarea" autosize v-model="addForm.publish_date" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="size">
          <el-input type="textarea" autosize v-model="addForm.size"></el-input>
        </el-form-item>
        <el-form-item label="" prop="version">
          <el-input type="textarea" autosize v-model="addForm.version"></el-input>
        </el-form-item>
        <el-form-item label="" prop="author">
          <el-input type="textarea" autosize v-model="addForm.author"></el-input>
        </el-form-item>
        <el-form-item label="" prop="translator">
          <el-input type="textarea" autosize v-model="addForm.translator" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input type="textarea" autosize v-model="addForm.price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pages">
          <el-input type="textarea" autosize v-model="addForm.pages" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="catalog">
          <el-input type="textarea" autosize v-model="addForm.catalog" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="market_price">
          <el-input type="textarea" autosize v-model="addForm.market_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="member_price">
          <el-input type="textarea" autosize v-model="addForm.member_price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="discount">
          <el-input type="textarea" autosize v-model="addForm.discount"></el-input>
        </el-form-item>
        <el-form-item label="" prop="image_url">
          <el-upload class="upload-demo"
            :action="null"
            :before-upload="beforeUpload"
            :on-preview="handlePreview"
            :on-remove="handleRemove"

            list-type="picture">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="store_mount">
          <el-input type="textarea" autosize v-model="addForm.store_mount" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="pack_style">
          <el-input type="textarea" autosize v-model="addForm.pack_style" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="is_shelf">
         <el-select v-model="addForm.is_shelf" placeholder="" style="margin-right: 3%">
            <el-option
              v-for="item in options"
              :key="item.is_shelf"
              :label="item.label"
              :value="item.is_shelf">
            </el-option>
         </el-select>
        </el-form-item>
        <el-form-item label="ID" prop="book_category_id">
          <el-select v-model="addForm.book_category_id" placeholder="" style="margin-right: 3%">
            <el-option
              v-for="item in options1"
              :key="item.book_category_id"
              :label="item.label"
              :value="item.book_category_id">
            </el-option>
         </el-select>
        </el-form-item>
        <el-form-item label="" prop="cname" hidden>
          <el-input type="textarea" autosize v-model="addForm.cname" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" autosize v-model="addForm.description"></el-input>
        </el-form-item>
        <el-form-item label="cata" prop="cata">
          <el-input type="textarea" autosize v-model="addForm.cata" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" autosize v-model="addForm.content" ></el-input>
        </el-form-item>
         <el-form-item label="" prop="isbn">
          <el-input type="textarea" autosize v-model="addForm.isbn"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button style="margin-top:-70px" type="primary" @click="addBook"> </el-button>
     </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="infodialogVisible"
      width="50%" @close="infodialogClosed">
      <!-- -->
      <el-form :model="infoForm" ref="infoFormRef" label-width="100px">
        <el-form-item style="margin-top:-30px">
          <span class="editbtn">
            <el-button type="info" @click="changeType"> </el-button>
          </span>
        </el-form-item >
        <el-form-item label="ID" prop="bookid" style="margin-top:-20px">
          <el-input type="textarea" autosize v-model="infoForm.bookid" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input type="textarea" autosize v-model="infoForm.name" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="outline">
          <el-input type="textarea" autosize v-model="infoForm.outline" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="detail">
          <el-input type="textarea" autosize v-model="infoForm.detail" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="press">
          <el-input type="textarea" autosize v-model="infoForm.press" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="publish_date">
           <el-input type="textarea" autosize v-model="infoForm.publish_date" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="size">
          <el-input type="textarea" autosize v-model="infoForm.size" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="version">
          <el-input type="textarea" autosize v-model="infoForm.version" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="author">
          <el-input type="textarea" autosize v-model="infoForm.author" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="translator">
          <el-input type="textarea" autosize v-model="infoForm.translator" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input type="textarea" autosize v-model="infoForm.price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pages">
          <el-input type="textarea" autosize v-model="infoForm.pages" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="catalog">
          <el-input type="textarea" autosize v-model="infoForm.catalog" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="market_price">
          <el-input type="textarea" autosize v-model="infoForm.market_price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="member_price">
          <el-input type="textarea" autosize v-model="infoForm.member_price" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="discount">
          <el-input type="textarea" autosize v-model="infoForm.discount" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="image_url">
          <el-image
            style="width: 100px; height: 100px"
            :src="infoForm.image_url"
            :fit="fit"></el-image>
          <el-upload class="upload-demo"
            :action="null"
            :before-upload="beforeUpload"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            list-type="picture">
            <el-button size="small" type="primary" :disabled="isdisabled"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="store_mount">
          <el-input type="textarea" autosize v-model="infoForm.store_mount" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pack_style">
          <el-input type="textarea" autosize v-model="infoForm.pack_style" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="is_shelf">
         <el-input type="textarea" autosize v-model="infoForm.is_shelf" placeholder="1:0" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="book_category_id">
          <el-input type="textarea" autosize placeholder="1,2,3,4,5:,6,7,8,9" v-model="infoForm.book_category_id" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cname" :hidden = "!isdisabled">
          <el-input type="textarea" autosize v-model="infoForm.cname" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" autosize v-model="infoForm.description" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="cata" prop="cata">
          <el-input type="textarea" autosize v-model="infoForm.cata" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" autosize v-model="infoForm.content" :disabled="isdisabled"></el-input>
        </el-form-item>
         <el-form-item label="" prop="isbn">
          <el-input type="textarea" autosize v-model="infoForm.isbn" :disabled="isdisabled"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer" :hidden="isdisabled">
        <el-button style="margin-top:-70px" type="primary" @click="editBookInfo"> </el-button>
     </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      options: [{
        is_shelf: '1',
        label: ''
      }, {
        is_shelf: '0',
        label: ''
      }],
      is_shelf: '',
      // 
      options1: [{
        book_category_id: '1',
        label: ''
      }, {
        book_category_id: '2',
        label: ''
      }, {
        book_category_id: '3',
        label: ''
      }, {
        book_category_id: '4',
        label: ''
      }, {
        book_category_id: '5',
        label: ''
      }, {
        book_category_id: '6',
        label: ''
      }, {
        cate: '7',
        label: ''
      }, {
        book_category_id: '8',
        label: ''
      }, {
        book_category_id: '9',
        label: ''
      }
      ],
      book_category_id: '',
      // 
      queryInfo: {
        store_id: window.sessionStorage.getItem('storeid'),
        // 
        pagenum: 1,
        // 
        pagesize: 5,
        query: ''
      },
      booklist: [],
      total: 0,
      // 
      addDialogVisible: false,
      infodialogVisible: false,
      isdisabled: true,

      // 
      addForm: {
        book_category_id: '',
        store_id: '',
        name: '',
        outline: '',
        press: '',
        publish_date: '',
        detail: '',
        size: '',
        version: '',
        author: '',
        translator: '',
        price: '',
        pages: '',
        catalog: '',
        market_price: '',
        member_price: '',
        discount: '',
        image_url: '',
        store_mount: '',
        pack_style: '',
        is_shelf: '',
        cname: '',
        description: '',
        cata: '',
        content: '',
        isbn: ''
      },
      // 
      infoForm: {
        bookid: '',
        book_category_id: '',
        name: '',
        outline: '',
        press: '',
        publish_date: '',
        detail: '',
        size: '',
        version: '',
        author: '',
        translator: '',
        price: '',
        pages: '',
        catalog: '',
        market_price: '',
        member_price: '',
        discount: '',
        image_url: '',
        store_mount: '',
        pack_style: '',
        is_shelf: '',
        cname: '',
        description: '',
        cata: '',
        content: '',
        isbn: ''
      },
      // 
      addFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        outline: [
          { required: true, message: '', trigger: 'blur' }
        ],
        press: [
          { required: true, message: '', trigger: 'blur' }
        ],
        publish_date: [
          { required: true, message: '', trigger: 'blur' }
        ],
        detail: [
          { required: true, message: '', trigger: 'blur' }
        ],
        author: [
          { required: true, message: '', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        book_category_id: [
          { required: true, message: '', trigger: 'blur' }
        ],
        is_shelf: [
          { required: true, message: '', trigger: 'blur' }
        ],
        pages: [
          { required: true, message: '', trigger: 'blur' }
        ],
        store_mount: [
          { required: true, message: '', trigger: 'blur' }
        ],
        market_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        member_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        discount: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      fileList: [{ name: '', url: '' }]
    }
  },
  created () {
    this.getBookList()
  },
  methods: {
    // 
    async beforeUpload (image) {
      const pic = new FormData()
      pic.append('picture', image)
      const { data: res } = await this.axios.post('/UpPicture', pic)
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.addForm.image_url = res.url
      return false
    },
    // 
    async getBookList () {
      // 
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBook', {
        store_id: this.queryInfo.store_id,
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = ''
        } else {
          res.data[i].is_shelf = ''
        }
      })
    },
    // 
    async selectBookQuery () {
      // 
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBookQuery', {
        store_id: this.queryInfo.store_id,
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        query: this.queryInfo.query
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        console.log(res.data[i].author)
        console.log(res.data[i].is_shelf)
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = ''
        } else {
          res.data[i].is_shelf = ''
        }
      })
    },
    // pagesize
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookList()
    },
    // 
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookList()
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    infodialogClosed () {
      this.changeType()
      this.$refs.infoFormRef.resetFields()
    },
    // 
    addBook () {
      this.addForm.store_id = window.sessionStorage.getItem('storeid')
      this.addForm.cname = this.options1[this.addForm.book_category_id - 1].label
      this.$refs.addFormRef.validate(async valid => {
        console.log(this.addForm)
        if (!valid) return
        // 
        const { data: res } = await this.axios.post('/addBook', this.addForm)
        console.log(this.axios.post)
        console.log(res)
        if (res.status !== '1') {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.$refs.addFormRef.resetFields()
        // 
        this.getBookList()
      })
    },

    // 
    async showinfoDialog (id) {
      this.infodialogVisible = true
      console.log(id)
      const { data: res } = await this.axios.post('/selectBookinfo', {
        bookid: id
      }
      )
      if (res.status !== '1') {
        return this.$message.error('')
      }
      console.log(res)
      this.infoForm.author = res.author
      this.infoForm.bookid = res.bookid
      this.infoForm.image_url = res.image_url
      this.infoForm.is_shelf = res.is_shelf
      this.infoForm.name = res.name
      this.infoForm.outline = res.outline
      this.infoForm.press = res.press
      this.infoForm.price = res.price
      this.infoForm.publish_date = res.publish_date
      this.infoForm.detail = res.detail
      this.infoForm.size = res.size
      this.infoForm.version = res.version
      this.infoForm.translator = res.translator
      this.infoForm.pages = res.pages
      this.infoForm.catalog = res.catalog
      this.infoForm.market_price = res.market_price
      this.infoForm.member_price = res.member_price
      this.infoForm.discount = res.discount
      this.infoForm.store_mount = res.store_mount
      this.infoForm.pack_style = res.pack_style
      this.infoForm.cname = res.cname
      this.infoForm.book_category_id = res.book_category_id
      this.infoForm.description = res.description
      this.infoForm.cata = res.cata
      this.infoForm.content = res.content
      this.infoForm.isbn = res.isbn
    },

    // 
    editBookInfo () {
      // this.changeType()
      this.$refs.infoFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.axios.post('/modifyBook', {
          book_id: this.infoForm.bookid,
          book_category_id: this.infoForm.book_category_id,
          store_id: window.sessionStorage.getItem('storeid'),
          name: this.infoForm.name,
          outline: this.infoForm.outline,
          detail: this.infoForm.detail,
          press: this.infoForm.press,
          author: this.infoForm.author,
          publish_date: this.infoForm.publish_date,
          size: this.infoForm.size,
          version: this.infoForm.version,
          translator: this.infoForm.translator,
          isbn: this.infoForm.isbn,
          price: this.infoForm.price,
          pages: this.infoForm.pages,
          catalog: this.infoForm.catalog,
          market_price: this.infoForm.market_price,
          member_price: this.infoForm.member_price,
          discount: this.infoForm.discount,
          image_url: this.infoForm.image_url,
          store_mount: this.infoForm.store_mount,
          pack_style: this.infoForm.pack_style,
          is_shelf: this.infoForm.is_shelf,
          cname: this.infoForm.cname,
          description: this.infoForm.description,
          cata: this.infoForm.cata,
          content: this.infoForm.content
        })
        console.log(res)
        if (res.status !== '1') {
          this.$message.error('')
        }
        // 
        this.$message.success('')
        // 
        this.infodialogVisible = false
        // 
        this.getBookList()
      })
    },

    // ID
    async removeBookbyId (id) {
      // 
      const confirmresult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmresult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.axios.post('/deleteBook', {
        bookid: id
      })
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getBookList()
    },

    // 
    changeType () {
      this.isdisabled = !this.isdisabled
    },
    //
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    //
    handlePreview (file) {
      console.log(file)
    }
  }
}
</script>

