<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
       <!--  -->
       <span></span>
       <el-table :data="ranklist">
         <el-table-column label="" type="index" width="95px"></el-table-column>
         <el-table-column label="" prop="bookname"></el-table-column>
       </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      ranklist: {
        bookname: ''
      }
    }
  },
  created () {
    this.getrankList()
  },
  methods: {
    async getrankList () {
      const { data: res } = await this.axios.post('/selectStoreTop', {
        store_id: window.sessionStorage.getItem('storeid')
      })
      if (res.status !== '1') {
        return this.$message.error('')
      }
      console.log(res.data)
      this.ranklist = res.data
    }
  }
}
</script>

