<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
       <el-row gutter="20">
         <el-col :span="8">
           <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList">
             <el-button slot="append" icon="el-icon-search" @click="selectOrdersQuery"></el-button>
          </el-input>
          </el-col>
          <el-col :span="8">
            <el-select v-model="queryInfo.qstatus" clearable @clear="getOrderList" placeholder=" ">
            <el-option
              v-for="item in status"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :hidden="item.hidden">
            </el-option>
            </el-select>
            <el-button icon="el-icon-search" @click="selectOrdersStatus"></el-button>
          </el-col>
       </el-row>

       <!--  -->
       <el-table :data="orderlist" border stripe>
         <el-table-column label="ID" prop="orderid"></el-table-column>
         <el-table-column label="" prop="username"></el-table-column>
         <el-table-column label="" prop="bookname"></el-table-column>
         <el-table-column label="" prop="payment"></el-table-column>
         <el-table-column label="" prop="create_time"></el-table-column>
         <el-table-column label="" prop="status">
           <template slot-scope="scope">
             <span style="float:center"> {{status[scope.row.status].label}}</span>
             <div style="color: #409EFF;cursor:pointer;float:center" @click="changestatus(scope.row)"></div>
           </template>
         </el-table-column>
         <el-table-column label="" width="180px">
            <template slot-scope="scope">
             <!--  -->
             <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
             <el-button type="primary" icon="el-icon-edit" size="mini" content="" @click="showEditDialog(scope.row.orderid)"></el-button>
             </el-tooltip>
             <!--  -->
             <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
             <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeOrderbyId(scope.row.orderid)"></el-button>
             </el-tooltip>
             <!--  -->
             <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
               <el-button type="warning" icon="el-icon-more" size="mini" @click="showinfoDialog(scope.row.orderid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>

       <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2,5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editdialogVisible"
      width="50%" @close="editDialogClosed">
      <!-- -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="120px">
        <el-form-item label="ID" prop="orderid">
          <el-input autosize v-model="editForm.orderid" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_name">
          <el-input autosize v-model="editForm.receiver_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_mobile">
          <el-input autosize v-model="editForm.receiver_mobile"></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_state">
          <el-input autosize v-model="editForm.receiver_state"></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_city" >
          <el-input autosize v-model="editForm.receiver_city"></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_district">
          <el-input autosize v-model="editForm.receiver_district"></el-input>
        </el-form-item>
        <el-form-item label="" prop="receiver_address">
          <el-input autosize v-model="editForm.receiver_address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editdialogVisible = false"> </el-button>
        <el-button type="primary" @click="editBookInfo"> </el-button>
     </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editstatusdialogVisible"
      width="50%" @close="editstatusDialogClosed">
      <!-- -->
      <el-form :model="editstatusForm" :rules="editstatusFormRules" ref="editstatusFormRef" label-width="120px">
        <el-form-item label="ID" prop="newstatus">
           <el-select v-model="editstatusForm.newstatus" clearable placeholder=" ">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="canclechangestatus"> </el-button>
        <el-button type="primary" @click="confirmchangeStatus"> </el-button>
     </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="infodialogVisible"
      width="50%" @close="infoDialogClosed">
      <!-- -->
      <div id="exportPdf" ref="exportPdf">
        <el-form :model="infoForm" ref="infoFormRef" label-width="100px">
          <el-form-item label="ID" prop="orderid">
            <el-input autosize v-model="infoForm.orderid" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_name">
            <el-input autosize v-model="infoForm.receiver_name" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_mobile">
            <el-input autosize v-model="infoForm.receiver_mobile" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_state">
            <el-input autosize v-model="infoForm.receiver_state" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_city" >
            <el-input autosize v-model="infoForm.receiver_city" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_district">
            <el-input autosize v-model="infoForm.receiver_district" disabled></el-input>
          </el-form-item>
          <el-form-item label="" prop="receiver_address">
            <el-input autosize v-model="infoForm.receiver_address" disabled></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="infodialogVisible = false"> </el-button>
        <el-button type="primary" @click="savePdf()"> </el-button>
     </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // value
      status: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: '',
        hidden: true
      }, {
        value: '6',
        label: '',
        hidden: true
      }, {
        value: '7',
        label: ''
      }],
      options: [{}],
      // value
      options1: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }, {
        value: '7',
        label: ''
      }],
      options2: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '7',
        label: ''
      }],
      options3: [{
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }],
      options4: [{
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }],
      options5: [{
        value: '4',
        label: ''
      }],
      options6: [{
        value: '5',
        label: ''
      }],
      value: '',
      // 
      queryInfo: {
        query: '',
        store_id: window.sessionStorage.getItem('storeid'),
        // 
        pagenum: 1,
        // 
        pagesize: 5,
        qstatus: ''
      },
      orderlist: [],
      total: 0,
      // 
      editdialogVisible: false,
      infodialogVisible: false,
      editstatusdialogVisible: false,

      editstatusForm: {
        newstatus: ''
      },
      // 
      editForm: {
        orderid: '',
        receiver_name: '',
        receiver_mobile: '',
        receiver_state: '',
        receiver_city: '',
        receiver_district: '',
        receiver_address: ''
      },
      infoForm: {
        orderid: '',
        receiver_name: '',
        receiver_mobile: '',
        receiver_state: '',
        receiver_city: '',
        receiver_district: '',
        receiver_address: ''
      },
      // 
      editFormRules: {
        receiver_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        receiver_mobile: [
          { required: true, message: '', trigger: 'blur' }
        ],
        receiver_state: [
          { required: true, message: '', trigger: 'blur' }
        ],
        receiver_city: [
          { required: true, message: '', trigger: 'blur' }
        ],
        receiver_district: [
          { required: true, message: '', trigger: 'blur' }
        ],
        receiver_address: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editstatusFormRules: {
        newstatus: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    // 
    canclechangestatus () {
      this.editstatusdialogVisible = false
    },
    // 
    savePdf () {
      this.$PDFSave(this.$refs.exportPdf, window.sessionStorage.getItem('orderid'))
      this.$message.success('')
    },
    // 
    async getOrderList () {
      const { data: res } = await this.axios.post('/selectOrders', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      console.log(res)
      this.$message.success('')
      this.orderlist = res.data
      this.total = parseInt(res.sum)
    },
    // 
    async selectOrdersQuery () {
      const { data: res } = await this.axios.post('/selectOrdersQuery', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        query: this.queryInfo.query
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.orderlist = res.data
      console.log(status)
      this.total = parseInt(res.sum)
    },
    // 
    async selectOrdersStatus () {
      const { data: res } = await this.axios.post('/selectOrdersStatus', {
        store_id: this.queryInfo.store_id,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        status: this.queryInfo.qstatus
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.orderlist = res.data
      console.log(status)
      this.total = parseInt(res.sum)
    },
    // 
    changestatus (order) {
      window.sessionStorage.setItem('orderid', order.orderid)
      console.log(order.status)
      if (order.status === 0) {
        this.options = this.options1
        this.editstatusdialogVisible = true
      } else if (order.status === 1) {
        this.options = this.options2
        this.editstatusdialogVisible = true
      } else if (order.status === 2) {
        this.options = this.options3
        this.editstatusdialogVisible = true
      } else if (order.status === 3) {
        this.options = this.options4
        this.editstatusdialogVisible = true
      } else if (order.status === 4) {
        this.editstatusdialogVisible = false
        this.$message.error('')
      } else if (order.status === 5) {
        this.editstatusdialogVisible = false
        this.$message.error('')
      } else if (order.status === 7) {
        this.editstatusdialogVisible = false
        this.$message.error('')
      }
    },
    // 
    async confirmchangeStatus () {
      const { data: res } = await this.axios.post('/modifyOrderStatus', {
        order_id: window.sessionStorage.getItem('orderid'),
        status: this.editstatusForm.newstatus
      })
      console.log(window.sessionStorage.getItem('orderid'))
      console.log(this.editstatusForm.newstatus)
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.editstatusdialogVisible = false
      this.getOrderList()
    },
    // pagesize
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    async showEditDialog (id) {
      this.editdialogVisible = true
      console.log(id)
      const { data: res } = await this.axios.post('/selectOrderShipping', {
        order_id: id
      })
      if (res.status !== '1') {
        // return this.$message.error('')
      }
      this.editForm.orderid = res.data[0].order_id
      this.editForm.receiver_name = res.data[0].receiver_name
      this.editForm.receiver_mobile = res.data[0].receiver_mobile
      this.editForm.receiver_state = res.data[0].receiver_state
      this.editForm.receiver_city = res.data[0].receiver_city
      this.editForm.receiver_district = res.data[0].receiver_district
      this.editForm.receiver_address = res.data[0].receiver_address
    },
    // 
    async showinfoDialog (id) {
      window.sessionStorage.setItem('orderid', id)
      this.infodialogVisible = true
      console.log(id)
      console.log(this.infodialogVisible)
      const { data: res } = await this.axios.post('/selectOrderShipping', {
        order_id: id
      })
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      console.log(res)
      this.infoForm.orderid = res.data[0].order_id
      this.infoForm.receiver_name = res.data[0].receiver_name
      this.infoForm.receiver_mobile = res.data[0].receiver_mobile
      this.infoForm.receiver_state = res.data[0].receiver_state
      this.infoForm.receiver_city = res.data[0].receiver_city
      this.infoForm.receiver_district = res.data[0].receiver_district
      this.infoForm.receiver_address = res.data[0].receiver_address
    },
    // 
    exportorder (id) {
      this.infodialogVisible = false
      this.$message.success('')
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editstatusDialogClosed () {
      this.$refs.editstatusFormRef.resetFields()
    },
    // 
    infoDialogClosed () {
      this.$refs.infoFormRef.resetFields()
    },
    // 
    editBookInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        // 
        const { data: res } = await this.axios.post('/modifyOrderShipping', {
          order_id: this.editForm.orderid,
          receiver_name: this.editForm.receiver_name,
          receiver_mobile: this.editForm.receiver_mobile,
          receiver_state: this.editForm.receiver_state,
          receiver_city: this.editForm.receiver_city,
          receiver_district: this.editForm.receiver_district,
          receiver_address: this.editForm.receiver_address
        })
        if (res.status !== '1') {
          this.$message.error('')
        }
        console.log(res)
        // 
        this.editdialogVisible = false
        // 
        this.getOrderList()
        // 
        this.$message.success('')
      })
    },
    // ID
    async removeOrderbyId (id) {
      // 
      const confirmresult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmresult !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.axios.post('/deleteOrder', {
        order_id: id
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getOrderList()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
