<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
       <img src="../../assets/logo.png">
      </div>
      <!--  -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!--  -->
        <el-form-item prop="email">
          <el-input v-model="loginForm.email" placeholder="" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" placeholder="" prefix-icon="el-icon-lock"
          type="password" ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click=login()></el-button>
          <el-button type="info" @click="resetLoginForm()"></el-button>
        </el-form-item>
        <!--  -->
        <div>
          <el-link type="info" @click="changeType"></el-link>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      loginForm: {
        email: '22301126@bjtu.edu.cn',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        email: [
          { required: true, message: '', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {

    // 
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
      // this.$nextTick(() => { this.$refs.loginFormRef.resetFields() })
    },
    // 
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.axios.post('/login', this.loginForm)
        console.log(res)
        if (res.status !== '1') return this.$message.error('')
        // console.log(res)
        this.$message.success('')
        window.sessionStorage.setItem('email', this.loginForm.email)
        console.log(res.username)
        window.sessionStorage.setItem('username', res.uesrname)
        window.sessionStorage.setItem('storeid', res.store_id)
        // token
        // console.log(res)
        this.$router.push('/home')
      })
    },
    // 
    changeType () {
      this.$router.push('/register')
    }
  }
}
</script>

<style lang='less' scoped>
.login_container{
  background-color: #2b4b6b;
  height: 100%;
}

.login_box{
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);

  .avatar_box{
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position :absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.btns {
  display: flex;
  justify-content: flex-end;
  height: 70%
}
.login_form {
  position: absolute;
  bottom: 0px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
</style>
