<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
       <!--  -->
       <el-table :data="catelist" border stripe>
         <el-table-column label="" prop="categoryid"></el-table-column>
         <el-table-column label="" prop="cname"></el-table-column>
         <el-table-column label="" width="140px">
            <template slot-scope="scope">
             <!--  -->
             <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
             <el-button type="primary" icon="el-icon-folder-opened" size="mini" @click="getBookList(scope.row.categoryid)"></el-button>
             </el-tooltip>
           </template>
         </el-table-column>
       </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="infodialogVisible"
      width="65%" @close="infoDialogClosed">
      <!-- -->
      <el-card class="box-card">
       <!--  -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="ID" prop="bookid"></el-table-column>
         <el-table-column label="" prop="bookname"></el-table-column>
       </el-table>

       <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      catelist: [],
      booklist: [],
      queryInfo: {
        store_id: window.sessionStorage.getItem('storeid'),
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      infoForm: {
        bookid: '',
        bookname: ''
      },
      total: 0,
      infodialogVisible: false
    }
  },
  created () {
    this.getcateList()
  },
  methods: {
    async getcateList () {
      const { data: res } = await this.axios.post('/selectBookCate')
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.catelist = res.data
    },

    async getBookList (id) {
      this.infodialogVisible = true
      window.sessionStorage.setItem('cateid', id)
      const { data: res } = await this.axios.post('/selectBookByCate', {
        category_id: id,
        store_id: window.sessionStorage.getItem('storeid'),
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      console.log(res)
    },
    async getBookListin () {
      this.infodialogVisible = true
      const { data: res } = await this.axios.post('/selectBookByCate', {
        category_id: window.sessionStorage.getItem('cateid'),
        // store_id: window.sessionStorage.getItem('storeid'),
        store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize
      })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
    },
    // pagesize
    handleSizeChange (newSize) {
      console.log(newSize)
      this.queryInfo.pagesize = newSize
      this.getBookListin()
    },
    // 
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookListin()
    }
  }
}
</script>

<style lang="less" scoped>

</style>
