<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="changeType"></el-button>
      </div>
    <!-- -->
      <el-form :model="shopForm" :rules="shopFormRules" ref="shopFormRef" label-width="80px">
        <el-form-item label="" prop="store_name">
          <el-input v-model="shopForm.store_name" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="store_position">
          <el-input autosize v-model="shopForm.store_position" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="store_phone_num">
          <el-input v-model="shopForm.store_phone_num" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="store_telephone">
          <el-input v-model="shopForm.store_telephone" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="created">
          <el-input v-model="shopForm.created" disabled></el-input>
        </el-form-item>
      </el-form>
      <span class="bottombtn" :hidden="isdisabled">
        <el-button  type="info"  @click="changeType()"> </el-button>
        <el-button  type="primary" @click="editShop()"> </el-button>
      </span>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      isdisabled: true,
      // 
      shopForm: {
        store_name: '',
        store_position: '',
        store_phone_num: '',
        store_telephone: '',
        created: ''
      },
      created () {
        this.getshopInfo()
      },
      // 
      shopFormRules: {
        store_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        store_position: [
          { required: true, message: '', trigger: 'blur' }
        ],
        store_phone_num: [
          { required: true, message: '', trigger: 'blur' }
        ],
        store_telephone: [
          { required: true, message: '', trigger: 'blur' }
        ],
        created: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getshopInfo()
  },
  methods: {
    async getshopInfo () {
      // 
      const { data: res } = await this.axios.post('/selectStoreInfo', { email: window.sessionStorage.getItem('email') })
      console.log(res)
      if (res.status !== '1') {
        return this.$message.error('')
      }
      // this.$message.success('')
      this.shopForm = res.data[0]
      window.sessionStorage.setItem('storeid', res.data[0].store_id)
      console.log(res)
    },
    changeType () {
      this.isdisabled = !this.isdisabled
      this.getshopInfo()
    },
    async editShop () {
      const { data: res } = await this.axios.post('/modifyStoreInfo', {
        store_id: window.sessionStorage.getItem('storeid'),
        store_name: this.shopForm.store_name,
        store_position: this.shopForm.store_position,
        store_phone_num: this.shopForm.store_phone_num,
        store_telephone: this.shopForm.store_telephone
      })
      console.log(res)
      if (res.status !== '1') {
        this.$message.error('')
        this.isdisabled = !this.disabled
        this.getshopInfo()
      }
      // this.$message.success('')
      this.isdisabled = !this.disabled
      this.getshopInfo()
    }
  }
}
</script>

<style lang="less" scoped>
.bottombtn {
  float: right;
  margin-bottom: 10px;
}
</style>
