/* eslint-disable no-irregular-whitespace */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">

       <!--  -->
       <el-table :data="booklist" border stripe>
         <el-table-column label="ID" prop="book_id"></el-table-column>
         <el-table-column label="" prop="title"></el-table-column>
         <el-table-column label="" prop="press"></el-table-column>
         <el-table-column label="" prop="author"></el-table-column>
         <el-table-column label="" prop="outline"></el-table-column>
         <el-table-column label="" prop="publish_date"></el-table-column>
         <el-table-column label="" width="180px">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-notebook-2" size="mini" @click="lendbook(scope.row)"></el-button>
              </el-tooltip>
           </template>
         </el-table-column>
       </el-table>
        <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1,2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total,prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="lenddialogVisible"
      width="50%" @close="lenddialogClosed">
      <!-- -->
      <el-form :model="lendForm" ref="lendFormRef" label-width="100px" :rules="lendFormRules" >
        <el-form-item label="ID" prop="book_id" style="margin-top:-20px">
          <el-input type="textarea" autosize v-model="lendForm.book_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input type="textarea" autosize v-model="lendForm.name" :disabled="isdisabled"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input type="textarea" autosize v-model="lendForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <el-form style="text-align:right">
        <el-select v-model="lendForm.days" placeholder="" style="margin-right: 3%">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
         </el-select>
          <el-button style="margin-top:-70px" type="primary" @click="confirmlend()"> </el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        // 
        pagenum: 1,
        query: ''
      },
      options: [{
        value: '1',
        label: '1'
      }, {
        value: '2',
        label: '2'
      }, {
        value: '3',
        label: '3'
      }, {
        value: '4',
        label: '4'
      }, {
        value: '5',
        label: '5'
      }, {
        value: '6',
        label: '6'
      }, {
        value: '7',
        label: '7'
      }],
      booklist: [],
      total: 0,
      lenddialogVisible: false,
      isdisabled: true,

      lendForm: {
        book_id: '',
        name: '',
        mobile: '',
        days: ''
      },
      lendFormRules: {
        days: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getBookList()
  },
  methods: {

    // 
    lendbook (book) {
      this.lendForm.book_id = book.book_id
      this.lendForm.name = book.title
      this.lenddialogVisible = true
    },
    // 
    async confirmlend () {
      const { data: res } = await this.axios.post('/lendbook', {
        user_email: '17301115@bjtu.edu.cn',
        book_id: this.lendForm.book_id,
        book_name: this.lendForm.name,
        lend_time: this.lendForm.days,
        telephone: this.lendForm.mobile
      }
      )
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
    },
    // 
    async getBookList () {
      // 
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBookAll', {
        page: this.queryInfo.pagenum
      })
      console.log(res)
      if (res.status_code !== '1') return this.$message.error('')
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = ''
        } else {
          res.data[i].is_shelf = ''
        }
      })
    },
    // 
    async selectBookQuery () {
      // 
      console.log(this.queryInfo.pagesize)
      const { data: res } = await this.axios.post('/selectBookQuery', {
        store_id: this.queryInfo.store_id,
        // store_id: 1,
        pagenum: this.queryInfo.pagenum,
        pagesize: this.queryInfo.pagesize,
        query: this.queryInfo.query
      })
      console.log(res)
      if (res.status !== '1') return this.$message.error('')
      this.$message.success('')
      this.booklist = res.data
      this.total = parseInt(res.sum)
      this.booklist.forEach((item, i) => {
        console.log(res.data[i].author)
        console.log(res.data[i].is_shelf)
        if (res.data[i].is_shelf === 1) {
          res.data[i].is_shelf = ''
        } else {
          res.data[i].is_shelf = ''
        }
      })
    },
    // 
    handleCurrentChange (newPage) {
      console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getBookList()
    },
    lenddialogClosed () {
      this.$refs.lendFormRef.resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
.editbtn{
  float: right;
  line-height: 15px;
}
</style>
