<template>
    <div>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!--  ECharts  DOM -->
            <div id="main" style="width: 1000px;height:400px;"></div>
            <div id="second" style="width: 1000px;height:400px;"></div>
        </el-card>
    </div>
</template>
<script>
// echrat
import echarts from 'echarts'
export default {
  data () {
    return {
      data: [],
      data2: []
    }
  },
  created () {},
  // 
  async mounted () {
    // domecharts
    this.data = []
    this.data2 = []
    var myChart = echarts.init(document.getElementById('main'))
    const { data: res } = await this.axios.post('/storeOrderSum', {
      store_id: window.sessionStorage.getItem('storeid')
    })
    if (res.status !== '1') {
      return this.$message.error('')
    }
    console.log(res.data)
    for (const item1 of res.data) {
      this.data.push(item1.)
      this.data2.push(item1.)
    }
    // 
    var option = {
      title: {
        text: ''
      },
      tooltip: {},
      legend: {
        data: ['']
      },
      xAxis: {
        data: this.data
      },
      yAxis: {},
      series: [{
        name: '',
        type: 'bar',
        data: this.data2
      }]
    }
    // 
    myChart.setOption(option)
  },
  methods () {}
}
</script>
